import { Button, Input, VStack, Text, Heading } from "@chakra-ui/react";
import { toaster } from "./toaster";
import { useState, useEffect } from "react";
import { riddles } from "./AllRiddles";

interface RiddlesProps {
  day: number;
}

export const Riddles = ({ day }: RiddlesProps) => {
  const [answer, setAnswer] = useState("");
  const [solved, setSolved] = useState(false);

  // Najdi hdanku pro dan den
  const riddle = riddles.find((r) => r.day === day);

  // Reset pi zmn dne
  useEffect(() => {
    setAnswer("");
    setSolved(false);
    // Zkontroluj, zda je hdanka u vyeena v localStorage
    const solvedRiddles = JSON.parse(
      localStorage.getItem("solvedRiddles") || "[]"
    );
    if (solvedRiddles.includes(day)) {
      setSolved(true);
    }
  }, [day]);

  // Pokud nen hdanka pro dan den, zobraz zprvu
  if (!riddle) {
    return (
      <VStack gap={4} p={4}>
        <Text>Pro tento den nen dn hdanka.</Text>
      </VStack>
    );
  }

  const checkAnswer = () => {
    const userAnswer = answer.trim().toLowerCase();
    const correctAnswer = riddle.correctAnswer.toLowerCase();

    if (userAnswer === correctAnswer) {
      toaster.create({
        title: "Sprvn! ",
        description: "Odpov je sprvn!",
      });
      setSolved(true);
      // Ulo do localStorage
      const solvedRiddles = JSON.parse(
        localStorage.getItem("solvedRiddles") || "[]"
      );
      if (!solvedRiddles.includes(day)) {
        solvedRiddles.push(day);
        localStorage.setItem("solvedRiddles", JSON.stringify(solvedRiddles));
      }
    } else {
      toaster.create({
        title: "patn! ",
        description: "Zkus to znovu!",
      });
    }
  };

  const handleKeyDown = (e: React.KeyboardEvent<HTMLInputElement>) => {
    if (e.key === "Enter" && !solved) {
      checkAnswer();
    }
  };

  return (
    <VStack gap={4} p={4} align="stretch">
      <Heading size="md"> Hdanka slo {day}</Heading>
      <Text fontSize="lg" fontWeight="medium">
        {riddle.riddle}
      </Text>
      {solved ? (
        <VStack gap={2} align="stretch">
          <Text color="green.500" fontWeight="bold">
             Sprvn odpov: {riddle.correctAnswer}
          </Text>
          <Text color="gray.600" fontSize="sm">
            Tato hdanka je u vyeena!
          </Text>
        </VStack>
      ) : (
        <VStack gap={2} align="stretch">
          <Input
            type="text"
            placeholder="Zadej svou odpov..."
            value={answer}
            onChange={(e) => setAnswer(e.target.value)}
            onKeyDown={handleKeyDown}
            disabled={solved}
          />
          <Button
            onClick={checkAnswer}
            colorScheme="blue"
            disabled={!answer.trim() || solved}
          >
            Ovit odpov
          </Button>
        </VStack>
      )}
    </VStack>
  );
};
