import { useState, useEffect } from "react";

export const useSolvedRiddles = () => {
  const [solvedRiddles, setSolvedRiddles] = useState<number[]>(() => {
    const saved = localStorage.getItem("solvedRiddles");
    return saved ? JSON.parse(saved) : [];
  });

  // Sleduj zmny v localStorage pro vyeen hdanky
  useEffect(() => {
    const checkSolved = () => {
      const saved = localStorage.getItem("solvedRiddles");
      if (saved) {
        setSolvedRiddles(JSON.parse(saved));
      }
    };

    // Zkontroluj pi naten
    checkSolved();

    // Sleduj zmny v localStorage
    const handleStorageChange = (e: StorageEvent) => {
      if (e.key === "solvedRiddles") {
        checkSolved();
      }
    };

    window.addEventListener("storage", handleStorageChange);

    // Pro zmny v aktulnm okn pouij interval (storage event nefunguje ve stejnm okn)
    const interval = setInterval(checkSolved, 300);

    return () => {
      window.removeEventListener("storage", handleStorageChange);
      clearInterval(interval);
    };
  }, []);

  // Funkce pro kontrolu, zda je hdanka vyeena
  const isRiddleSolved = (day: number) => {
    return solvedRiddles.includes(day);
  };

  return {
    solvedRiddles,
    isRiddleSolved,
  };
};

